'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import Link from 'next/link'

interface DashboardStats {
  totalCustomers: number
  openTickets: number
  pendingInvoices: number
  todayAppointments: number
  revenueThisMonth: number
  completedTicketsThisWeek: number
}

interface RecentActivity {
  id: string
  type: 'ticket' | 'customer' | 'invoice'
  title: string
  description: string
  time: string
  status?: string
}

export default function DashboardPage() {
  const [stats, setStats] = useState<DashboardStats>({
    totalCustomers: 0,
    openTickets: 0,
    pendingInvoices: 0,
    todayAppointments: 0,
    revenueThisMonth: 0,
    completedTicketsThisWeek: 0,
  })
  const [recentActivity, setRecentActivity] = useState<RecentActivity[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    fetchDashboardData()
  }, [])

  const fetchDashboardData = async () => {
    try {
      // Simulate API calls - replace with actual API endpoints
      setStats({
        totalCustomers: 156,
        openTickets: 23,
        pendingInvoices: 8,
        todayAppointments: 5,
        revenueThisMonth: 12450,
        completedTicketsThisWeek: 18,
      })

      setRecentActivity([
        {
          id: '1',
          type: 'ticket',
          title: 'New Ticket Created',
          description: 'Laptop screen replacement for John Doe',
          time: '2 minutes ago',
          status: 'open'
        },
        {
          id: '2',
          type: 'customer',
          title: 'New Customer Registered',
          description: 'Sarah Johnson added to customer database',
          time: '15 minutes ago'
        },
        {
          id: '3',
          type: 'invoice',
          title: 'Invoice Paid',
          description: 'Invoice #INV-001 paid by Mike Wilson',
          time: '1 hour ago',
          status: 'paid'
        },
        {
          id: '4',
          type: 'ticket',
          title: 'Ticket Completed',
          description: 'Desktop repair completed for Tech Corp',
          time: '2 hours ago',
          status: 'completed'
        },
      ])
    } catch (error) {
      console.error('Error fetching dashboard data:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const getStatusColor = (status?: string) => {
    switch (status) {
      case 'open': return 'bg-blue-100 text-blue-800'
      case 'completed': return 'bg-green-100 text-green-800'
      case 'paid': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getActivityIcon = (type: string) => {
    switch (type) {
      case 'ticket': return 'T'
      case 'customer': return 'C'
      case 'invoice': return '$'
      default: return 'â€¢'
    }
  }

  if (isLoading) {
    return (
      <div className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {[...Array(4)].map((_, i) => (
            <Card key={i} className="animate-pulse">
              <CardContent className="p-6">
                <div className="h-4 bg-muted rounded w-3/4 mb-2"></div>
                <div className="h-8 bg-muted rounded w-1/2"></div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold">Dashboard</h1>
        <p className="text-muted-foreground">
          Welcome to your repair shop management dashboard
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Customers</CardTitle>
            <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
              <span className="text-blue-600 font-bold text-sm">C</span>
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalCustomers}</div>
            <p className="text-xs text-muted-foreground">
              +12% from last month
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Open Tickets</CardTitle>
            <div className="w-8 h-8 bg-orange-100 rounded-lg flex items-center justify-center">
              <span className="text-orange-600 font-bold text-sm">T</span>
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.openTickets}</div>
            <p className="text-xs text-muted-foreground">
              {stats.completedTicketsThisWeek} completed this week
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Pending Invoices</CardTitle>
            <div className="w-8 h-8 bg-red-100 rounded-lg flex items-center justify-center">
              <span className="text-red-600 font-bold text-sm">$</span>
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.pendingInvoices}</div>
            <p className="text-xs text-muted-foreground">
              Awaiting payment
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Revenue This Month</CardTitle>
            <div className="w-8 h-8 bg-green-100 rounded-lg flex items-center justify-center">
              <span className="text-green-600 font-bold text-sm">$</span>
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${stats.revenueThisMonth.toLocaleString()}</div>
            <p className="text-xs text-muted-foreground">
              +8% from last month
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Quick Actions & Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
            <CardDescription>
              Common tasks to get you started
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-3">
            <Link href="/dashboard/customers">
              <Button variant="outline" className="w-full justify-start">
                <div className="w-6 h-6 bg-blue-100 rounded flex items-center justify-center mr-3">
                  <span className="text-blue-600 font-bold text-xs">+</span>
                </div>
                Add New Customer
              </Button>
            </Link>
            <Link href="/dashboard/tickets">
              <Button variant="outline" className="w-full justify-start">
                <div className="w-6 h-6 bg-orange-100 rounded flex items-center justify-center mr-3">
                  <span className="text-orange-600 font-bold text-xs">T</span>
                </div>
                Create New Ticket
              </Button>
            </Link>
            <Link href="/dashboard/invoices">
              <Button variant="outline" className="w-full justify-start">
                <div className="w-6 h-6 bg-green-100 rounded flex items-center justify-center mr-3">
                  <span className="text-green-600 font-bold text-xs">$</span>
                </div>
                Generate Invoice
              </Button>
            </Link>
            <Link href="/dashboard/appointments">
              <Button variant="outline" className="w-full justify-start">
                <div className="w-6 h-6 bg-purple-100 rounded flex items-center justify-center mr-3">
                  <span className="text-purple-600 font-bold text-xs">A</span>
                </div>
                Schedule Appointment
              </Button>
            </Link>
          </CardContent>
        </Card>

        {/* Recent Activity */}
        <Card>
          <CardHeader>
            <CardTitle>Recent Activity</CardTitle>
            <CardDescription>
              Latest updates from your repair shop
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentActivity.map((activity) => (
                <div key={activity.id} className="flex items-start space-x-3">
                  <div className="w-8 h-8 bg-muted rounded-lg flex items-center justify-center flex-shrink-0">
                    <span className="text-muted-foreground font-bold text-xs">
                      {getActivityIcon(activity.type)}
                    </span>
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between">
                      <p className="text-sm font-medium">{activity.title}</p>
                      {activity.status && (
                        <Badge variant="secondary" className={`text-xs ${getStatusColor(activity.status)}`}>
                          {activity.status}
                        </Badge>
                      )}
                    </div>
                    <p className="text-sm text-muted-foreground">{activity.description}</p>
                    <p className="text-xs text-muted-foreground mt-1">{activity.time}</p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Today's Appointments */}
      <Card>
        <CardHeader>
          <CardTitle>Today's Appointments</CardTitle>
          <CardDescription>
            {stats.todayAppointments} appointments scheduled for today
          </CardDescription>
        </CardHeader>
        <CardContent>
          {stats.todayAppointments > 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground">
                You have {stats.todayAppointments} appointments today.
              </p>
              <Link href="/dashboard/appointments">
                <Button className="mt-4">View All Appointments</Button>
              </Link>
            </div>
          ) : (
            <div className="text-center py-8">
              <p className="text-muted-foreground">No appointments scheduled for today.</p>
              <Link href="/dashboard/appointments">
                <Button variant="outline" className="mt-4">Schedule Appointment</Button>
              </Link>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
